.Nav {
    position: absolute;
    color: #ff83f9;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 30px;
    z-index: 999;
}
.Signout_btn {
    font-size: 25px;
    color: rgb(24, 22, 22);
    cursor: pointer;
}
@media (max-width: 1024px){
    .Nav {
        position: fixed;
        top: 0;
        left: 0;
        padding: 10px;
        background-color: rgba(255, 105, 180, 0.37);
        -webkit-backdrop-filter: blur(20px);
                backdrop-filter: blur(20px);
    }
}

@media (max-width: 767.98px){
    .des_show {
        display: none;
    }
}